body {
  background: #ffffff;
}
.MuiInputLabel-root {
  font-family: "Nunito" !important;
  font-style: normal !important;
  font-weight: 400 !important;
  font-size: 20px !important;
  line-height: 27px !important;
  /* color: #92959a !important; */
}

.MuiTextField-root {
  box-sizing: border-box !important;
  background: #ffffff;
  border-bottom: 2px solid !important;
  border-radius: 2px !important;
}
.light-Textfield {
  border-bottom: 0.5px solid #939eaa !important;
  border-radius: 2px !important;
}
.dark-Textfield {
  border-bottom: 2px solid #000000 !important;
  border-radius: 2px !important;
}
.dark-Textfield:disabled {
  border-bottom: 2px solid #92959a !important;
}
.MuiButton-outlined {
  height: 60px !important;
  padding: 18px 27px !important;
  background: #ffffff !important;
  border: 1px solid #92959a !important;
  border-radius: 5px !important;
  font-weight: 500;
  font-size: 20px;
  line-height: 27px;
}
.MuiButton-contained {
  height: 60px !important;
  padding: 18px 27px !important;
  gap: 7px !important;
  background: #46448d !important;
  /* opacity: 0.5 !important; */
  border: 1px solid #46448d !important;
  border-radius: 5px !important;
  font-weight: 700 !important;
  font-size: 20px !important;
  line-height: 27px !important;
}

.MuiSelect-select {
  /* background: #ffffff !important;
  border: 0.5px solid #92959a !important;
  border-radius: 2px !important; */
}
.MuiSelect-standard {
  min-width: 100px !important;
  border: none !important;
  border-radius: none !important;
}
.MuiDataGrid-columnSeparator {
  display: none !important;
}

.MuiDataGrid-cell {
  border: none !important;
  border-left: 1px solid #d9d9d9 !important;
  border-radius: 0px !important;
}
.MuiDataGrid-columnHeader {
  background: #798593 !important;
  color: #ffffff !important;
  border: none !important;
  border: 1px solid #d9d9d9 !important;
  border-radius: 5px 5px 0px 0px !important;
}

.MuiToolbar-regular {
  height: 78px !important;
  color: black;
  background: #ffffff !important;
  border: 0.1px solid #abb2bb !important;
  border-left: none !important;
}

.MuiAppBar-root {
  box-shadow: none !important;
}
.MuiListItem-root {
  padding: 31px !important;
  font-weight: 400 !important;
  font-size: 20px !important;
  line-height: 27px !important;
}
.MuiListItem-root:hover {
  background-color: #46448d;
  color: white;
  border-right: 6px solid orange;
}

.sidebar-head {
  padding: 31px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
}
.breadcrumb-text {
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;

  color: #abb2bb;
}

.nav-icon {
  padding: 15px;
}

.lead-list {
  display: flex;
  align-items: center !important;
  color: #795718 !important;
}

.MuiListItemText-primary {
  font-weight: 600 !important;
  font-size: 18px !important;
  line-height: 25px !important;
}

.lead-list:hover {
  background: #edecf4 !important;
  border-width: 0.2px 0px 0.2px 0.2px !important;
  border-style: solid !important;
  border-color: #abb2bb !important;
  color: #00a3ff !important;
}

.container {
  display: flex;
  justify-content: start;
  background: transparent !important;
  height: 100% !important;
  padding: 36px;
  overflow-y: scroll;
}
.card-container {
  height: 100% !important;
}

.containerHead {
  height: 90px;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-left: 50px;
  background: #46448d;
  border-radius: 10px 10px 0px 0px;
  font-weight: 700;
  font-size: 30px;
  line-height: 41px;
  color: #ffffff;
}

.containerContent {
  padding: 30px;
}
.unverifiedlead-tab {
  font-weight: 400;
  font-size: 22px;
  line-height: 30px;
  height: 76px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #46448d;
}
.unverifiedlead-tab-clicked {
  background: #46448d;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 76px;
  color: #ffffff;
}

.editLeadStepper {
  display: flex;
  align-items: center;
  padding: 45px;
  height: 45px;
  background: rgba(205, 175, 121, 0.19);
}

.MuiStepIcon-root.Mui-active {
  color: #00bd79 !important;
}
